/**
 * @name Helpers
 * @type {Object}
 * @description
 * Helpers for ext-bbm-transactions-details-ng
 */

import { negativeSignKey } from './debit-credit-sign';

/**
 * @name getTransactionToDisplay
 * @description
 * Processes the list of transactions.
 *
 * @type {function}
 * @param {Object} transaction The original transaction from the API
 * @returns {Object} The processed transaction
 * @inner
 */
const getTransactionToDisplay = transaction => ({
  ...transaction,
  amount: transaction.amount * (transaction.creditDebitIndicator === negativeSignKey ? -1 : 1),
});

export default ({ stateContainer }) => {
  /**
   * @description
   * Returns the current `transaction` from the state
   *
   * @name Helpers#getTransaction
   * @type {function}
   * @returns {Object} transactions Transaction from the state
   */
  const getTransaction = stateContainer.createSelector(
    state => getTransactionToDisplay(state.transaction.data)
  );

  return {
    getTransaction,
  };
};



