import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ handleRequest, service }) => ({
  /**
   * @name Intents#showTransactionDetails
   * @description Handles the intent to show the transaction details
   * @type {function}
   */
  showTransactionDetails: handleRequest(Intent.SHOW_TRANSACTION_DETAILS, transaction => {
    if (transaction) {
      service.setSelectedTransaction(transaction);
    } else {
      throw new Error(
        'Transaction is undefined. Please pass a transaction object as the payload of the intent'
      );
    }
  }),
});



